<template>
  <div class="home" style="user-select: none;">
    <Header @getStatusEnd="getStatus"></Header>
    <mt-swipe :auto="4000" style="height: 200px;">
      <mt-swipe-item style="line-height: 200px;background: rosybrown;">
        <img src="../assets/img/1.jpg"/>
      </mt-swipe-item>
      <mt-swipe-item style="line-height: 200px;background: #ffd450;">
        <img src="../assets/img/2.jpg"/>
      </mt-swipe-item>
      <mt-swipe-item style="line-height: 200px;background: darkorange;">
        <img src="../assets/img/3.jpg"/>
      </mt-swipe-item>
    </mt-swipe>
    <div class="main-view" v-if="showTsk">
      <div class="view-item">
        <div>
          <img :src="rate" @click="goRegister"/>
        </div>
        <span class="title">申请代理商</span>
      </div>
      <div class="view-item">
        <div>
          <img :src="task" @click="goAnalyse"/>
        </div>
        <span class="title">销售报表</span>
      </div>
      <div class="view-item">
        <div>
          <img :src="near"/>
        </div>
        <span class="title">充值与提现</span>
      </div>
    </div>
    <div class="main-table" v-if="showTsk && mis">
      <div class="main-table-title">我的申请</div>
      <div class="view-table-item">
        <table>
          <tr>
            <td>序号</td>
            <td>名称</td>
            <td>当前状态</td>
            <td>操作</td>
          </tr>
          <tr>
            <td>1</td>
            <td>代理商申请</td>
            <td style="color: red;">审核中</td>
            <td style="color: #26a2ff;">详情</td>
          </tr>
        </table>
      </div>
    </div>
    <div class="main-table">
      <div class="main-table-title">周边代理</div>
      <el-amap vid="amap" class="amap" :center="center" :zoom="zoom" :plugin="plugin" :events="events">
        <div v-for="item in position">
          <el-amap-marker  vid="amapDemo" :position="item.adr"></el-amap-marker>
        </div>
      </el-amap>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import rate from './../assets/img/rate.png'
import task from './../assets/img/task.png'
import near from './../assets/img/near.png'
export default {
  name: 'HomePage',
  components: { Header},
  data () {
    var that = this
    return {
      titleMsg: '渠道管理系统',
      showTsk: false,
      mis: false,
      rate: '',
      task: '',
      near: '',
      // 地图
      center: [114.03176, 22.52979],
      position: [
        {id: '1', adr: [114.03176, 22.52979]},
        {id: '2', adr: [114.03186, 22.53180]},
        {id: '3', adr: [114.02886, 22.52980]},
        {id: '4', adr: [114.02826, 22.53280]},
        {id: '5', adr: [114.03376, 22.52850]},
        {id: '5', adr: [114.02936, 22.52719]}
      ],
      zoom: 16,
      events: {
        init: o => {
          o.getCity(result => {
            // console.log(result)
          })
        },
        'click': (e) => {
          // 点击地图的时候，获取点击的经纬度，并将地图中心点移自此处
          let m = e.lnglat
          that.center = [m.lng, m.lat]
        }
      },
      plugin: [{
        // 获取当前位置，只能支持https访问获取
        pName: 'Geolocation',
        events: {
          init(o) {
            // o 是高德地图定位插件实例
            o.getCurrentPosition((status, result) => {
              if (result && result.position) {
                that.center = [result.position.lng, result.position.lat]
              }
            })
          }
        }
      }]
    }
  },
  methods: {
    goRegister() {
      this.$router.push('/register')
      this.showTsk = false
    },
    goAnalyse() {
      this.$router.push('/analyse')
      this.showTsk = false
    },
    getStatus() {
      if(sessionStorage.getItem('_userInfo') != '' && sessionStorage.getItem('_userInfo') != null) {
        this.rate = rate
        this.task = task
        this.near = near
        this.showTsk = true
        if(sessionStorage.getItem('_mis') != '' && sessionStorage.getItem('_mis') != null) {
          // 显示消息
          this.mis = true
        }
      } else {
        this.showTsk = false
      }
    }
  },
  mounted() {
    this.getStatus()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main-view{
    display: flex;
    flex-direction: row;
    /*justify-content: space-around;*/
    flex-wrap: wrap;
  }
  .main-view .view-item {
    position: relative;
    width: 33%;
    margin: 20px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-view .view-item img {
  }
  .main-view .view-item .title {
    color: #9E9E9E;
  }
  .main-table{}
  .main-table .main-table-title{
    text-align: left;padding: 8px 5px;border-bottom: 0px solid #d2d2d2;border-top: 1px solid #d2d2d2;
  }
  .main-table .view-table-item{}
  .main-table .view-table-item table {
    width: 100%;
    border-collapse:collapse;
  }
  .main-table .view-table-item table tr td{
    padding: 5px 0px;
  }
  .main-table .view-table-item table tr:first-child{
    background: #ccdcff;
    color: #253459;
  }
  .main-table .view-table-item table tr:nth-child(2n){
    background: #f2faff;
    color: #253459;
  }
  .amap{
    height: 350px;
  }
</style>



